import uniCalendar from "@/components/uni-calendar/uni-calendar"
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			price: 5,
			selected: [{
				date: '2019-11-10',
				info: '已打卡',					
			},
			{
				date: '2019-11-11',
				info: '已打卡',					
			},				
			],
			costList: [{
					id: 1,
					name: '周',
					prcie: '10'
				},
				{
					id: 1,
					name: '月',
					prcie: '100'
				}, {
					id: 1,
					name: '季度',
					prcie: '300'
				}, {
					id: 1,
					name: '年',
					prcie: '1900'
				},
			],
		};
	},
	onLoad(options) {
		this.pages = options.pages
		this.getToday = this.getToday()
		console.log(this.getToday, '00000000')
		if(this.pages =='daka'){
			uni.setNavigationBarTitle({
				title:'打卡'
			})
		}
	},
	methods: {
		getToday() {
			var now = new Date();
			var year = now.getFullYear();
			var month = now.getMonth() + 1;
			var day = now.getDate();
			if (month < 10) {
				month = '0' + month;
			};
			if (day < 10) {
				day = '0' + day;
			}
			return year + '-' + month + '-' + day
		},

		onBuyVip(item) {

		},
		changeDate(e) {
			
			
			for (let item of this.selected) {
					if(item.date==this.getToday){
						console.log(item.date==this.getToday,'222')
						uni.showToast({
							title:'你已经打过卡了哟~',
							icon:'none'
						})
						return 
					}
			}
			
			if (this.getToday == e.fulldate) {
				uni.showModal({
					title: '打卡提示',
					content: '确定去打卡',
					success: (res) => {
						if (res.confirm) {
							//调起打卡接口
							let json={
								date:e.fulldate,
								info:'已打卡'
							}
							
							this.selected.push(json)
						}
					}
				})
			}

			console.log(e.fulldate);
		}
	}
}
